export default ".slideUpBtn {\n    padding: 12px 24px;\n    background-color: transparent;\n    border: 2px solid hsl(243, 80%, 62%);\n    border-radius: 6px;\n    position: relative;\n    overflow: hidden;\n    transition: all 0.5s cubic-bezier(1,.15,.34,.92)\n}\n\n.slideUpBtn span {\n    display: inline-block;\n    transition: inherit;\n    color: hsl(243, 80%, 62%);\n}\n\n.slideUpBtn:hover span {\n    opacity: 0;\n    transform: translateY(-100%)\n}\n\n.slideUpBtn::before {\n    content: \"\";\n    background-color: hsl(243, 80%, 62%);;\n    height: 100%;\n    left: 0;\n    position: absolute;\n    top: 0;\n    transform: translateY(100%);\n    transition: inherit;\n    width: 100%;\n    transition: transform 0.5s cubic-bezier(1,.15,.34,.92)\n}\n\n.slideUpBtn::after {\n    content: 'Hover me';\n    align-items: center;\n    display: flex;\n    height: 100%;\n    justify-content: center;\n    left: 0;\n    position: absolute;\n    top: 0;\n    transition: inherit;\n    transform: translateY(100%);\n    width: 100%;\n\n}\n\n.slideUpBtn:hover::before {\n    transform: translateY(0) scale(3);\n    transition-delay: .025s;\n}\n\n.slideUpBtn:hover::after {\n    opacity: 1;\n    color: hsl(222, 100%, 95%);\n    transform: translateY(0);\n}";