export default ".arrowScrollDown svg {\n    fill: hsl(243, 80%, 62%);\n    animation: arrowAnimation 1.5s infinite ease-out;\n}\n\n\n@keyframes arrowAnimation {\n    0% {\n      transform: translateY(-20px);\n      opacity: 0\n    }\n    30% {\n      opacity: 1\n    }\n    60% {\n      opacity: 1\n    }\n    to {\n      -webkit-transform: translateY(10px);\n      transform: translateY(10px);\n      opacity: 0\n    }\n  }";