export default ".borderMarker {\n    position: relative;\n  }\n\n  .borderMarker span {\n      z-index: 1;\n      position: relative;\n  }\n\n  .borderMarker::after {\n    content: \"\";\n    position: absolute;\n    left: -0.1px;\n    right: -0.1px;\n    bottom: 0;\n    height: 100%;\n    transform: scaleY(.3);\n    transition: transform .6s cubic-bezier(0.53, 0.21, 0, 1);\n    transform-origin: bottom;\n    background-color: hsl(222, 100%, 93%);\n  }\n\n  .borderMarker:hover::after {\n    transform: scaleY(1);\n  }\n";