export default ".borderFade {\n    padding-bottom: 1px;\n    color: hsl(222, 25%, 14%);\n    position: relative;\n}\n\n.borderFade::after {\n    content: '';\n    position: absolute;\n    height: 2px;\n    width: 100%;\n    left: 0;\n    bottom: 0;\n    opacity: 0;\n    transform: translateY(3px);\n    background: hsl(243, 80%, 62%);\n    transition: opacity 0.2s ease, transform 0.2s ease;\n}\n\n.borderFade:hover::after {\n    opacity: 1;\n    transform: translateY(0);\n}\n";