export default ".buttonOverlay {\n    padding: 12px 24px;\n    position: relative;\n    color: hsl(243, 80%, 62%);\n}\n\n.buttonOverlay span {\n    position: relative;\n    z-index: 1;\n}\n\n.buttonOverlay::before {\n    content: '';\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    top: 5px;\n    right: 5px;\n    background-color: hsl(222, 100%, 95%);\n    transition: transform .2s ease-in-out;\n}\n\n.buttonOverlay::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n    border: 1px solid #474E51;\n    transition: transform .2s ease-in-out;\n}\n\n.buttonOverlay:hover::before {\n    transform: translate(6px, -4px);\n}\n\n.buttonOverlay:hover::after {\n    transform: translate(-6px, 4px);\n}\n";