export default ".radiusPulse {\n    padding: 15px;\n    position: relative;\n}\n\n.radiusPulse::before {\n    content: \"\";\n    position: absolute;\n    top: -1px;\n    bottom: -1px;\n    right: -1px;\n    left: -1px;\n    background-color: hsl(243, 80%, 62%);\n    border-radius: 50%;\n    transform: scale(0);\n    transition: transform .3s cubic-bezier(.215,.61,.355,1);\n}\n\n.radiusPulse::after {\n    content: \"\";\n    position: absolute;\n    top: -1px;\n    bottom: -1px;\n    right: -1px;\n    left: -1px;\n    border: 2px solid hsl(243, 80%, 62%);\n    border-radius: 50%;\n    transform: scale(1);\n    opacity: 1;\n    transition: opacity .3s cubic-bezier(.215,.61,.355,1),transform .3s cubic-bezier(.215,.61,.355,1);\n}\n\n.radiusPulse:hover::before {\n    transform: scale(1);\n}\n\n.radiusPulse:hover::after {\n    transform: scale(1.2);\n    opacity: 0;\n}\n";